import { Guest } from "../interfaces/guest";
import { getGuests } from "../repo/guestList";

const container = document.querySelector(".container") as HTMLDivElement;

const addGuest = document.getElementById("add-guest") as HTMLButtonElement;

const eventId = sessionStorage.getItem('editEventId')!;

const guest = getGuests(eventId);


displayGuest(guest);
addGuest.addEventListener("click", function(){
    window.location.href="../dist/guest.html";
})


function displayGuest(guest : Guest[]){

    guest.forEach(g => {

        const box = document.createElement("div");
        box.classList.add("box");
        const left = document.createElement("div");
        left.classList.add("left");
        const right = document.createElement("div");
        right.innerHTML = '<div><button id="edit">Edit</button></div><div><button id="delete">Delete</button></div>';
        right.classList.add("right");
        const guestname = document.createElement("div");
        guestname.textContent = g.guestName;
        const guestemail = document.createElement("div");
        guestemail.textContent = g.guestEmail;
        const guestStatus = document.createElement("div");
        guestStatus.textContent = g.guestStatus;
        left.appendChild(guestname);
        left.appendChild(guestemail);
        left.appendChild(guestStatus);

        box.appendChild(left);
        box.appendChild(right);

        container.appendChild(box);

    })


}

